import React, { useState, useEffect } from 'react';
import { Container, Form, Button, Alert, Card, Row, Col } from 'react-bootstrap';
import './Settings.css';

function Settings() {
  const [openaiKey, setOpenaiKey] = useState('');
  const [emailSettings, setEmailSettings] = useState({
    email: '',
    password: '',
    smtpServer: 'smtp.gmail.com',
    smtpPort: '587'
  });
  const [message, setMessage] = useState(null);
  const [messageType, setMessageType] = useState('');

  useEffect(() => {
    // LocalStorage
    const savedOpenaiKey = localStorage.getItem('openai_api_key');
    if (savedOpenaiKey) {
      setOpenaiKey(savedOpenaiKey);
    }

    const savedEmailSettings = localStorage.getItem('email_settings');
    if (savedEmailSettings) {
      try {
        setEmailSettings(JSON.parse(savedEmailSettings));
      } catch (e) {
        console.error('Failed to parse email settings:', e);
      }
    }
  }, []);

  const handleOpenAISubmit = (e) => {
    e.preventDefault();
    if (!openaiKey) {
      setMessage('OpenAI API');
      setMessageType('danger');
      return;
    }

    // LocalStorage
    localStorage.setItem('openai_api_key', openaiKey);
    setMessage('OpenAI API');
    setMessageType('success');
  };

  const handleEmailSubmit = (e) => {
    e.preventDefault();
    if (!emailSettings.email || !emailSettings.password) {
      setMessage('');
      setMessageType('danger');
      return;
    }

    // LocalStorage
    localStorage.setItem('email_settings', JSON.stringify(emailSettings));
    setMessage('');
    setMessageType('success');
  };

  const clearSettings = () => {
    // 
    localStorage.removeItem('openai_api_key');
    localStorage.removeItem('email_settings');
    localStorage.removeItem('user_subscriptions');
    localStorage.removeItem('paper_history');
    
    setOpenaiKey('');
    setEmailSettings({
      email: '',
      password: '',
      smtpServer: 'smtp.gmail.com',
      smtpPort: '587'
    });
    
    setMessage('');
    setMessageType('warning');
  };

  return (
    <Container className="settings-container">
      <h1 className="text-center mb-4"></h1>
      
      {message && (
        <Alert variant={messageType} onClose={() => setMessage(null)} dismissible>
          {message}
        </Alert>
      )}
      
      <Row>
        <Col md={6} className="mb-4">
          <Card>
            <Card.Header as="h5">OpenAI API</Card.Header>
            <Card.Body>
              <Form onSubmit={handleOpenAISubmit}>
                <Form.Group className="mb-3">
                  <Form.Label>OpenAI API</Form.Label>
                  <Form.Control 
                    type="password" 
                    placeholder="OpenAI API" 
                    value={openaiKey}
                    onChange={(e) => setOpenaiKey(e.target.value)}
                  />
                  <Form.Text className="text-muted">
                    API
                  </Form.Text>
                </Form.Group>
                <Button variant="primary" type="submit">
                  API
                </Button>
              </Form>
            </Card.Body>
          </Card>
        </Col>
        
        <Col md={6} className="mb-4">
          <Card>
            <Card.Header as="h5"></Card.Header>
            <Card.Body>
              <Form onSubmit={handleEmailSubmit}>
                <Form.Group className="mb-3">
                  <Form.Label></Form.Label>
                  <Form.Control 
                    type="email" 
                    placeholder="your.email@gmail.com" 
                    value={emailSettings.email}
                    onChange={(e) => setEmailSettings({...emailSettings, email: e.target.value})}
                  />
                </Form.Group>
                
                <Form.Group className="mb-3">
                  <Form.Label>/</Form.Label>
                  <Form.Control 
                    type="password" 
                    placeholder="" 
                    value={emailSettings.password}
                    onChange={(e) => setEmailSettings({...emailSettings, password: e.target.value})}
                  />
                  <Form.Text className="text-muted">
                    Gmail
                  </Form.Text>
                </Form.Group>
                
                <Form.Group className="mb-3">
                  <Form.Label>SMTP</Form.Label>
                  <Form.Control 
                    type="text" 
                    placeholder="smtp.gmail.com" 
                    value={emailSettings.smtpServer}
                    onChange={(e) => setEmailSettings({...emailSettings, smtpServer: e.target.value})}
                  />
                </Form.Group>
                
                <Form.Group className="mb-3">
                  <Form.Label>SMTP</Form.Label>
                  <Form.Control 
                    type="text" 
                    placeholder="587" 
                    value={emailSettings.smtpPort}
                    onChange={(e) => setEmailSettings({...emailSettings, smtpPort: e.target.value})}
                  />
                </Form.Group>
                
                <Button variant="primary" type="submit">
                  
                </Button>
              </Form>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      
      <div className="text-center mt-4 mb-5">
        <Button variant="danger" onClick={clearSettings}>
          
        </Button>
      </div>
      
      <div className="settings-info">
        <h4></h4>
        <p>
          
          
        </p>
        <ul>
          <li></li>
          <li>API</li>
          <li></li>
          <li></li>
        </ul>
      </div>
    </Container>
  );
}

export default Settings; 